import React, { useState } from "react";
import './FeedbackForm.scss';

function FeedbackForm() {
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [message, setMessage] = useState("");
  // const [errorName, setErrorName] = useState ('');
  const [errorPhone, setErrorPhone] = useState ('');



  const handleSubmit = (e) => {
    e.preventDefault();

    console.log("Name:", name);
    console.log("Phone:", phone);
    console.log("Message:", message);
  };

  return (
    <form  className="form" method="post" action="#!" onSubmit={handleSubmit}>
      <label className="form__input-name">
        <input  className="form__name" type="text" value={name} placeholder="" onChange={(e) => setName(e.target.value)}/>
      </label>
      <br />
      <label>
        <input className="form__phone" value={phone}  onInput={(e) => {
                                setPhone(e.target.value)
                                setErrorPhone('')}}
                                type="tel" placeholder=""
                                pattern="^((8|\+374|\+994|\+995|\+375|\+7|\+380|\+38|\+996|\+998|\+993)[\- ]?)?\(?\d{3,5}\)?[\- ]?\d{1}[\- ]?\d{1}[\- ]?\d{1}[\- ]?\d{1}[\- ]?\d{1}(([\- ]?\d{1})?[\- ]?\d{1})?$"
                                title={errorPhone ? errorPhone : '   '}/>
      </label>
      <br />
      <div className="form__contacts">
        <label className="form__contact" htmlFor="contactMethod">C :</label>
          <select id="contactMethod" name="contactMethod">
              <option value="messengers">Telegram</option>
              <option value="vk">VKontakte</option>
              <option value="phone"></option>
          </select>
      </div>
      <div className="form__budget">
        <label  className="form__money" htmlFor="contactMethod"> </label>
          <select id="contactMethod" name="contactMethod">
              <option value="100"> 100 </option>
              <option value="200"> 100  200 </option>
              <option value="500"> 200  500 </option>
              <option value="1"> 500   1</option>
          </select>
      </div>
      <label >
        <textarea  className="form__message" value={message} placeholder=" "  onChange={(e) => setMessage(e.target.value)} />
      </label>
      <br />
      <button className="form__btn" type="submit"></button>
      <p className="form__text">       <a href="#!"> </a>.</p>
    </form>
  );
}

export default FeedbackForm;